import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("/PassGinie-Kobweb/")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.binayshaw.passginie.pages.HomePage() }
        ctx.router.register("/about") { com.binayshaw.passginie.pages.AboutPage() }
        ctx.router.register("/markdown") { com.binayshaw.passginie.pages.MarkdownPage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(com.binayshaw.passginie.components.sections.FooterStyle)
        ctx.theme.registerComponentStyle(com.binayshaw.passginie.components.sections.NavHeaderStyle)
        ctx.theme.registerComponentStyle(com.binayshaw.passginie.components.sections.NavItemStyle)
        ctx.theme.registerComponentStyle(com.binayshaw.passginie.components.styles.CustomRangeInputStyle)
        ctx.theme.registerComponentStyle(com.binayshaw.passginie.components.styles.EditorBoxStyle)
        ctx.theme.registerComponentStyle(com.binayshaw.passginie.components.styles.CheckBoxStyle)
        ctx.theme.registerComponentStyle(com.binayshaw.passginie.components.styles.PasswordViewBoxStyle)
        ctx.theme.registerComponentStyle(com.binayshaw.passginie.components.styles.AccessibilityButtonStyle)
        ctx.theme.registerComponentVariants(com.binayshaw.passginie.components.sections.NavButtonVariant)
        com.binayshaw.passginie.initSilk(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "PassGinie")
        ) { com.binayshaw.passginie.MyApp {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
